/* ==========================================================================
# Fullscreen search - start
========================================================================== */


/* Fullscreen search general style - start
========================================================================== */

.edgtf-fullscreen-search-holder {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 105;
    opacity: 0;
	
    .edgtf-fullscreen-search-table {
        display: table;
        width: 100%;
        height: 100%;
    }

    .edgtf-fullscreen-search-cell {
        @include edgtfTableCellLayout();
        text-align: center;
    }

    .edgtf-form-holder-inner {
        position: relative;
        width: 40%;
        margin: auto;

        .edgtf-line {
            position: absolute;
            left: 0;
            bottom: -2px;
            width: 0;
            border-bottom: 1px solid #707070;
            @include edgtfTransition(all .3s .2s);

            .edgtf-fullscreen-search-opened & {
                width: 100%;
                @include edgtfTransition(width 0.5s ease .6s);
            }
        }
    }

    .edgtf-field-holder {
        display: block;
        position: relative;
    }

    .edgtf-fullscreen-logo-holder{
    	position: absolute;
    	top: 45px;
    	left: 100px;
    }

    .edgtf-logo-wrapper{
    	a img{
    		display: block;
    		opacity: 0;

    		&.edgtf-dark-logo{
    			opacity: 1;
    		}
    	}
    }

    .edgtf-search-field {
        position: relative;
        height: 100%;
        width: 100%;
        color: $default-heading-color;
        font-size: 40px;
        font-weight: 700;
        font-family: $default-heading-font;
        letter-spacing: -0.04em;
        line-height: 2em;
        padding: 0;
        margin-bottom: 14px;
        border: 0;
        background: transparent;
        outline: none;
    }

    .edgtf-search-submit {
        position: absolute;
        right: 0;
        bottom: -20px;
        width: auto;
        font-size: 25px;
        line-height: 1em;
        color: #4a4a4a;
        background-color: transparent;
        border: none;
        outline: none;
        opacity: 0;
        cursor: pointer;
        @include edgtfTransition(all .3s .2s);
        
        &:hover {
            color: $default-heading-color;
        }

        .edgtf-fullscreen-search-opened & {
            opacity: 1;
            bottom: 20px;
            @include edgtfTransition(color 0.2s ease, opacity .2s ease-out 1.1s, bottom .4s ease-out .9s);
        }
    }

    .edgtf-fullscreen-search-close {
        position: absolute;
        top: 45px;
        right: 100px;
        color: $default-heading-color;
        font-size: 28px;
        opacity: 0;
        visibility: hidden;
	    z-index: 1;
	    @include edgtfTransition(opacity .2s ease-out);
        
        .admin-bar & {
            top: 72px;
        }
    
        &:hover {
            opacity: 0.9;
        }
    }
}
/* Fullscreen search general style - end
========================================================================== */

/* Fullscreen search fade style - start
========================================================================== */

.edgtf-search-fade .edgtf-fullscreen-search-holder {
    visibility: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    opacity: 0;
    z-index: 112;
    box-sizing: border-box;

    .edgtf-fullscreen-search-table {
        background-color: rgba(255, 255, 255, 1);
    }
}

.edgtf-search-fade-out.edgtf-search-fade .edgtf-fullscreen-search-holder {
    @include edgtfAnimation(search_fade_out .2s ease-out both);
}

.edgtf-search-fade-in.edgtf-search-fade .edgtf-fullscreen-search-holder.edgtf-animate {
    @include edgtfAnimation(search_fade_in .3s ease both);
}

.edgtf-search-fade .edgtf-fullscreen-search-holder.edgtf-animate .edgtf-fullscreen-search-close {
    opacity: 1;
    visibility: visible;
}

@-webkit-keyframes search_fade_out {
    0% {
        opacity: 1;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes search_fade_out {
    0% {
        opacity: 1;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@-webkit-keyframes search_fade_in {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
}

@keyframes search_fade_in {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
}

/* Fullscreen search fade style - end
========================================================================== */

/* ==========================================================================
# Fullscreen search - end
========================================================================== */
